"use client";

import { useState } from 'react';
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { useRouter } from 'next/navigation'; // Use next/navigation for App Router
import { useToast } from "@/hooks/use-toast";

// Mock authentication function (replace with actual API call)
const authenticateUser = async (email: string, password: string): Promise<{ success: boolean; role?: 'admin' | 'employee'; message?: string }> => {
  // Simulate network delay
  await new Promise(resolve => setTimeout(resolve, 500));

  // Replace with actual authentication logic against your backend (PHP/MySQL)
  if (email === "admin@example.com" && password === "password") {
    return { success: true, role: 'admin' };
  } else if (email === "employee@example.com" && password === "password") {
    return { success: true, role: 'employee' };
  } else {
    return { success: false, message: "Invalid credentials" };
  }
};

export default function LoginPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const router = useRouter();
  const { toast } = useToast();

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const authResult = await authenticateUser(email, password);

      if (authResult.success) {
        // Store user session/token (e.g., in localStorage or cookies)
        // For demo purposes, we'll just redirect based on role
        localStorage.setItem('userRole', authResult.role!); // Be cautious with localStorage for sensitive data

        toast({
          title: "Login Successful",
          description: `Redirecting to ${authResult.role} dashboard...`,
        });

        // Redirect based on role
        if (authResult.role === 'admin') {
          router.push('/admin/dashboard');
        } else if (authResult.role === 'employee') {
          router.push('/employee/dashboard');
        }
      } else {
        toast({
          variant: "destructive",
          title: "Login Failed",
          description: authResult.message || "An error occurred.",
        });
      }
    } catch (error) {
      console.error("Login error:", error);
      toast({
        variant: "destructive",
        title: "Login Error",
        description: "Something went wrong. Please try again.",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-4 sm:p-24 bg-background">
      <Card className="w-full max-w-sm shadow-lg">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl font-bold text-primary">Login to Gaea Realty and Consultants</CardTitle>
          <CardDescription>Enter your credentials below</CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleLogin} className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="email">Email</Label>
              <Input
                id="email"
                type="email"
                placeholder="admin@example.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                disabled={isLoading}
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="password">Password</Label>
              <Input
                id="password"
                type="password"
                placeholder="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                disabled={isLoading}
              />
            </div>
            <Button type="submit" className="w-full" disabled={isLoading}>
              {isLoading ? 'Logging in...' : 'Login'}
            </Button>
          </form>
        </CardContent>
      </Card>
    </main>
  );
}
